<template>
    <nav class="navbar navbar-expand-lg navbar-light bg-light">
    <div class="container-fluid">
        <a class="navbar-brand" href="#">Navbar</a>
        <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav me-auto mb-2 mb-lg-0">
            <li class="nav-item">
                <router-link class="nav-link" to="/">Home</router-link>
            </li>
            <li class="nav-item"  v-if="!isAuthenticated">
                <router-link class="nav-link" to="/login">Login</router-link>
            </li>
            <li class="nav-item"  v-if="!isAuthenticated">
                <router-link class="nav-link" to="/signup">Signup</router-link>
            </li>
            <li class="nav-item" v-if="isAuthenticated">
                <router-link class="nav-link" to="/posts">Posts</router-link>
            </li>
            <li class="nav-item" v-if="isAuthenticated">
                <a href="#" class="nav-link" @click.prevent="onLogout()">Logout</a>
            </li>
        </ul>
        <form class="d-flex">
            <input class="form-control me-2" type="search" placeholder="Search" aria-label="Search">
            <button class="btn btn-outline-success" type="submit">Search</button>
        </form>
        </div>
    </div>
    </nav>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import { IS_USER_AUTHENTICATED_GETTER } from '../store/storeconstants';
import { LOGOUT_ACTION } from '../store/storeconstants';
export default {
    computed: {
        ...mapGetters('auth', {
            isAuthenticated: IS_USER_AUTHENTICATED_GETTER,
        }),
    },
    methods: {
        ...mapActions('auth', {
            logout: LOGOUT_ACTION,
        }),
        onLogout() {
            this.logout();
            this.$router.replace('/login');
        },
    },
};
</script>